import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';


const routes: Routes = [
  {path: '',redirectTo: 'user',pathMatch: 'full'},
  {
    path: 'org',
    loadChildren: () =>
      import('./donation-org/donation-org.module').then(
        (m) => m.DonationOrgModule
      ),
  },
  {
    path: 'operating_unit',
    loadChildren: () =>
      import('./operating-unit/operating-unit.module').then(
        (m) => m.OperatingUnitModule
      ),
  },
  {
    path: 'bank',
    loadChildren: () => import('./bank/bank.module').then((m) => m.BankModule),
  },
  {
    path: 'deposit',
    loadChildren: () =>
      import('./receipt/deposit/deposit.module').then((m) => m.DepositModule),
  },
  {
    path: 'verification',
    loadChildren: () =>
      import('./receipt/deposit-verification/deposit-verification.module').then(
        (m) => m.DepositVerificationModule
      ),
  },
  {
    path: 'reconciliation',
    loadChildren: () =>
      import('./receipt/deposit-reconciliation/deposit-reconciliation.module').then(
        (m) => m.DepositReconciliationModule
      ),
  },
  {
    path: 'mark_ready',
    loadChildren: () =>
      import('./mark-ready-for-erp/mark-ready-for-erp.module').then(
         (m) => m.MarkReadyForErpModule
      ),
  },
  {
    path: 'posting',
    loadChildren: () =>
      import('./mark-ready-posting/mark-ready-posting.module').then(
         (m) => m.MarkReadyPostingModule
      ),
  },
  {
    path: 'knock_off',
    loadChildren: () =>
      import('./receipt/knock-off/knock-off.module').then(
        (m) => m.KnockOffModule
      ),
  },
  {
    path: 'eraseed_collection',
    loadChildren: () =>
      import('./receipt/eraseed-collection/eraseed-collection.module').then(
        m => m.EraseedCollectionModule
      )
  },
  {
    path: 'assigning-collectors',
    loadChildren: () =>
      import('./assigning-collectors/assigning-collectors.module').then(
        m => m.AssigningCollectorsModule
      )
  },
  {
    path: 'users-donation',
    loadChildren: () =>
      import('./user-donation/user-donation.module').then(
        m => m.UserDonationModule
      )
  },
  {
    path: 'acknowledgement-list',
    loadChildren: () =>
      import('./acknowledgement-list/acknowledgement-list.module').then(
        m => m.AcknowledgementListModule
      )
  },
  {
    path: 'box_management',
    loadChildren: () =>
      import('./box-management/box-management.module').then(
        m => m.BoxManagementModule
      )
  },
  {
    path: 'donation',
    loadChildren: () =>
      import('./donation/donation.module').then((m) => m.DonationModule),
  },

  {
    path: 'donor',
    loadChildren: () =>
      import('./donor/donor.module').then((m) => m.DonorModule),
  },
  {
    path: 'madni_basta',
    loadChildren: () =>
      import('./madni-basta/madni-basta.module').then(
        (m) => m.MadniBastaModule
      ),
  },
  {
    path: 'user',
    loadChildren: () =>
      import('./add-user/add-user.module').then((m) => m.AddUserModule),
  },
  {
    path: 'generate_batch',
    loadChildren: () =>
      import('./generate-batch/generate-batch.module').then(
        (m) => m.GenerateBatchModule
      ),
  },
  {
    path: 'allow_permission',
    loadChildren: () =>
      import('./allow-permission/allow-permission.module').then(
        (m) => m.AllowPermissionModule
      ),
  },
  {
    path: 'allow_system',
    loadChildren: () =>
      import('./allow-permission/allow-permission.module').then(
        (m) => m.AllowPermissionModule
      ),
  },
  {
    path: 'transaction',
    loadChildren: () =>
      import('./transaction/transaction.module').then(
        (m) => m.TransactionModule
      ),
  },
  {
    path: 'allow_purpose',
    loadChildren: () =>
      import('./allow-purpose/allow-purpose.module').then(
        (m) => m.AllowPurposeModule
      ),
  },
  {
    path: 'reports',
    loadChildren: () =>
      import('./donation-reports/donation-reports.module').then(
        (m) => m.DonationReportsModule
      ),
  },
  {
    path: 'error',
    loadChildren: () =>
      import('./error-x/error-x-routing.module').then(
        (m) => m.ErrorXRoutingModule
      ),
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class FeatureRoutingModule {}
